/* ========== Banner Section ========== */
.banner {
  background-size: cover;
  background-position: top;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  color: white;
}

.banner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6); /* Dark overlay */
  z-index: 0;
}

.wrapper {
  z-index: 1; /* Ensure content is above the overlay */
}

.bannerText {
  line-height: 1.6;
}

.bannerText h1 {
  font-size: 50px;
}

.bannerText p {
  font-size: 30px;
}

/* ========== Global Heading Styles ========== */
h2 {
  font-size: 36px;
  font-weight: bold;
  color: #50291a;
  text-align: center;
  margin-bottom: 20px;
}

/* ========== Button Styling ========== */
.button {
  display: inline-block;
  padding: 12px 24px;
  margin: 10px 0;
  background-color: #50291a;
  color: white;
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.button:hover {
  background-color: #69341f;
  transform: scale(1.05);
}

.button:active {
  transform: scale(0.98);
}

/* ========== About Section ========== */
.about-section-text {
  text-align: center;
  padding: 50px;
}

.about-section p {
  font-size: 20px;
  line-height: 1.6;
  padding: 20px 0;
}

/* ========== Partners Section ========== */
.partners-section {
  background: linear-gradient(135deg, #f4f4f4, #e8ded5);
  color: white;
  padding: 50px 0;
  text-align: center;
}

.partners-logos {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0;
}

.partners-logos img {
  max-width: 150px;
  max-height: 100px;
  object-fit: contain;
}

.partners-cta {
  margin-top: 20px;
}

.partners-cta p {
  font-size: 40px;
  color: #50291a;
  font-weight: bold;
  margin-bottom: 10px;
}

/* ========== Internship Section ========== */
.internship-section {
  /* background-color: #f9f9f9; */
  padding: 60px 20px;
  text-align: center;
}

.internship-text p {
  font-size: 18px;
  color: #333;
  line-height: 30px;
  max-width: 800px;
  margin: 0 auto;
}

/* Improved Internship List UI */
.internship-text ul {
  list-style: none;
  padding: 0;
  max-width: 800px;
  margin: 30px auto;
  text-align: left;
}

.internship-text li {
  font-size: 18px;
  line-height: 28px;
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border-radius: 8px;
  font-weight: bold;
  background: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
  transition: transform 0.3s ease;
}

.internship-text li:hover {
  transform: translateY(-2px);
}

.internship-text li::before {
  content: "‚úîÔ∏è";  
  font-size: 24px;
  color: #50291a;
}

.internship-text .button {
  margin-top: 20px;
  font-size: 18px;
}

/* ========== Partnership Applications Section ========== */
.partnership-section {
  background: linear-gradient(135deg, #f4f4f4, #e8ded5);
  padding: 60px 20px;
  text-align: center;
  border-top: 5px solid #50291a;
}

.partnership-text {
  max-width: 800px;
  margin: 0 auto;
}

.partnership-text h2 {
  font-size: 36px;
  font-weight: bold;
  color: #50291a;
  text-align: center;
  margin-bottom: 20px;
}

.partnership-text p {
  font-size: 18px;
  color: #333;
  line-height: 28px;
  margin-bottom: 20px;
}

.partnership-text ul {
  list-style: none;
  padding: 0;
  text-align: left;
  max-width: 700px;
  margin: 20px auto;
}

.partnership-text li {
  font-size: 18px;
  line-height: 26px;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 15px;
  background: #fff;
  border-left: 8px solid #69341f;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 12px;
}

.partnership-text li::before {
  content: "üåü";
  font-size: 20px;
  color: #69341f;
}

.partnership-text .button {
  margin-top: 20px;
  font-size: 18px;
  background-color: #69341f;
}

/* ========== Responsive Styling ========== */
@media only screen and (max-width: 580px) {
  .bannerText h1 {
    font-size: 40px;
  }

  .bannerText p {
    font-size: 20px;
  }

  .button,
  .about-section p {
    font-size: 15px;
  }

  .about-section p {
    padding: 10px 0;
    line-height: 1.4;
  }

  .about-section-text {
    padding: 50px 0;
  }

  h2 {
    font-size: 30px;
  }

  .internship-text li {
    font-size: 16px;
    line-height: 24px;
  }
}


@media only screen and (max-width: 425px) {
  .bannerText h1 {
    font-size: 30px;
  }

  .bannerText p {
    font-size: 15px;
  }

  .button {
    font-size: 12px;
  }
}

@media only screen and (max-width: 320px) {
  .bannerText h1 {
    font-size: 25px;
  }
}
